.more-photos {
  padding-bottom: 60px;
  @media screen and (min-width: 768px) {
    padding-bottom: 80px;
  }
  @media screen and (min-width: 1440px) {
    padding-bottom: 156px;
  }
}

.more-photos__title {
  color: getColor('title-text');
  margin-bottom: 16px;
  @media screen and (max-width: 767px) {
    @include font2(24px, 1.25, 700, null);
  }

  @media screen and (min-width: 768px) {
    @include font2(36px, 1.25, 700, null);
    margin-bottom: 32px;
  }

  @media screen and (min-width: 1200px) {
    @include font2(48px, 1.25, 700, null);
    margin-bottom: 60px;
  }
}

