.more-photos__list {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 16px;
  .more-photos__item {
    width: 280px;
    margin-left: auto;
    margin-right: auto;
    @media screen and (min-width: 768px) {
      width: calc((100% - 32px) / 3);
    }
    @media screen and (min-width: 1200px) {
      width: calc((100% - 32px) / 3);
    }
  }
}
